<script>
import  { Swiper, SwiperSlide} from 'vue-awesome-swiper'
import 'swiper/swiper.scss'

import F2 from '@antv/f2';

export default {
  name: "Home",
  components: {
    Swiper,
    SwiperSlide
  },
  props: {},
  data() {
    return {
      swiperOption: {
        loop: true,
        autoplay: {
          disableOnInteraction: false,
        },
        pagination: {
          el: '.swiper-pagination',
        },
        spaceBetween : 10,
      },
      swiperList: [
        { id: 2, scr: '', img: 'https://p9-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/e2777f578c8643c882a8e43a0da03da7~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
        { id: 5, scr: '', img: 'https://p1-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/c0c77d73e2274ed984a7ec4c00aeb0aa~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
        { id: 3, scr: '', img: 'https://p6-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/baab071eef1444eb98d59b7d8147b214~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
        { id: 1, scr: '', img: 'https://p9-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/aef565fb1f1e4c0d913276a8128ccd4a~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
        { id: 7, scr: '', img: 'https://p6-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/706a378cbbce484b943a2794521934e1~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
        { id: 6, scr: '', img: 'https://p1-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/b33b6ce541554f89ba28aedd0c2463d6~tplv-k3u1fbpfcp-watermark.image?imageView2/2/w/480/h/480/q/85/interlace/1' },
      ],
      weatherTodayData: {},
      weather_loading: true,
    }
  },
  created() {
    this.getAd()
  },
  mounted() {
    this.initChart()
  },
  methods: {
    getAd() {
      this.weather_loading = true

      let params = {
        key: '523630f6e97e4a4c08fcd91e9f005dd2',
      }
      this.$http.get('https://restapi.amap.com/v3/ip', {params}).then(res => {
        this.weatherTodayData.city = res.data.city
        this.getWeatherData(res.data.adcode)
      })
    },
    getWeatherData(city) {
      let params = {
        key: '523630f6e97e4a4c08fcd91e9f005dd2',
        city,
        extensions: 'all'
      }
      this.$http.get('https://restapi.amap.com/v3/weather/weatherInfo', {params}).then(res => {
        this.weatherTodayData = {
          ...res.data.forecasts[0].casts[0],
          province: res.data.forecasts[0].province,
          city: res.data.forecasts[0].city
        }
        this.weather_loading = false
      })
    },
    initChart() {
      document.getElementById('myChart').style.width = this.$refs.chartFoo.offsetWidth + 'px'
      document.getElementById('myChart').style.height = '260px'
      const data = [
        { date: '2020-11-25', value: 3 },
        { date: '2020-11-26', value: 6 },
        { date: '2020-11-27', value: 15 },
        { date: '2020-11-28', value: 29 },
        { date: '2020-11-29', value: 35 },
        
      ];

      const chart = new F2.Chart({
        id: 'myChart',
        pixelRatio: window.devicePixelRatio
      });

      chart.source(data, {
        value: {
          tickCount: 5,
          min: 0
        },
        date: {
          type: 'timeCat',
          range: [ 0, 1 ],
          tickCount: 3
        }
      });
      chart.tooltip({
        custom: true,
        showXTip: true,
        showYTip: true,
        snap: true,
        crosshairsType: 'xy',
        crosshairsStyle: {
          lineDash: [ 2 ]
        }
      });
      chart.axis('date', {
        label: function label(text, index, total) {
          const textCfg = {};
          if (index === 0) {
            textCfg.textAlign = 'left';
          } else if (index === total - 1) {
            textCfg.textAlign = 'right';
          }
          return textCfg;
        }
      });
      chart.line().position('date*value');
      chart.render();

    },
  },
};
</script>

